@import "../../assets/scss/app";

.tradeAnywhere {
  @include container;
  padding-top: 60px;

  @include desktop {
    padding-top: 160px;
  }

  .inner {
    @include inner;

    .title {
      @include title;
      @include desktop {
        width: 440px;
      }
    }

    .description {
      @include description;
      margin-top: 10px;

      @include desktop {
        margin-top: 20px;
      }

    }

    .buttons {
      margin-top: 20px;
      display: flex;
      flex-direction: column;

      @include desktop {
        margin-top: 50px;
        flex-direction: row;
      }

      button {
        @include desktop {
          width: 240px;
        }

        &:last-child {
          margin-top: 20px;

          @include desktop {
            margin-top: 0;
            margin-left: 36px;
          }
        }
      }
    }

  }
}